column;\r\n  position: relative;\r\n}\r\n.content {\r\n  flex: 1 1 auto;\r\n  border: 1px solid #000;\r\n}\r\n.container {\r\n  max-width: 1200px;\r\n  min-height: 900px;\r\n  margin: 0 auto;\r\n  position: relative;\r\n}\r\n/***************CONTAINER + ANIMATIONS*/\r\n.app__page {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  height: 100%;\r\n  width: 100%;\r\n  padding: 20px 0px;\r\n  border: 1px solid #000;\r\n  &.active {\r\n    animation: changePage 1s linear;\r\n  }\r\n  @keyframes changePage {\r\n    0% {\r\n      left: 0%;\r\n    }\r\n    50% {\r\n      left: -150%;\r\n    }\r\n    100% {\r\n      left: 0%;\r\n    }\r\n  }\r\n}\r\n/***************ELEMENTS*/\r\n.app__block {\r\n  height: 500px;\r\n  width: 415px;\r\n  overflow: hidden;\r\n  border-radius: 50px 5px 5px 5px;\r\n  &-container {\r\n    display: flex;\r\n    justify-content: space-around;\r\n    align-items: center;\r\n    flex-wrap: wrap;\r\n    row-gap: 20px;\r\n    height: calc(100% - 220px);\r\n    width: 100%;\r\n  }\r\n}\r\n\r\n.section {\r\n  height: 100%;\r\n  width: 100%;\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  &__logo-container {\r\n    height: 170px;\r\n    width: 250px;\r\n    margin-bottom: 20px;\r\n  }\r\n  &__logo {\r\n    height: 80%;\r\n    width: 100%;\r\n  }\r\n  &__info {\r\n    text-align: center;\r\n    text-transform: uppercase;\r\n  }\r\n}\r\n.block {\r\n  &__info {\r\n    height: 20%;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    text-transform: uppercase;\r\n  }\r\n  &-image {\r\n    height: 80%;\r\n  }\r\n  &__info.block__info-small {\r\n    font-size: 20px;\r\n    background: $element-small;\r\n  }\r\n  &__info-main {\r\n    background: $element-main;\r\n  }\r\n}\r\n\r\n.footer {\r\n  display: flex;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  height: 70px;\r\n  width: 100%;\r\n}\r\n\r\n.artists__section,