put-bar {\r\n    position: absolute;\r\n    z-index: 0;\r\n    top: 60%;\r\n    left: 0;\r\n    height: 8px;\r\n\r\n    background: $range-active;\r\n    border-radius: 3px;\r\n    transition: all 0.3s ease 0s;\r\n  }\r\n  &__volume-input-bar {\r\n    width: 100%;\r\n  }\r\n  &__timer-input-bar {\r\n    width: 40%;\r\n  }\r\n\r\n  &__volume-input-thumb,\r\n  &__timer-input-thumb {\r\n    position: absolute;\r\n    z-index: 1;\r\n    top: 48%;\r\n    width: 16px;\r\n    height: 16px;\r\n    background: $button;\r\n    border: 1px solid $white;\r\n    border-radius: 50%;\r\n    transform: translateX(-50%);\r\n    transition: all 0.3s ease 0s;\r\n  }\r\n\r\n  &__volume-input-thumb {\r\n    left: 100%;\r\n  }\r\n  &__timer-input-thumb {\r\n    left: 40%;\r\n  }\r\n\r\n  &__volume,\r\n  &__timer {\r\n    background: $white;\r\n    box-shadow: $shadow-active;\r\n    border-radius: 20px;\r\n    padding: 10px 40px;\r\n    justify-content: space-around;\r\n  }\r\n\r\n  &__volume-image,\r\n  &__timer-image {\r\n    height: 100px;\r\n  }\r\n  &__volume-image {\r\n    width: 100px;\r\n    background: url('./../../../assets/svg/volumeIcon.svg') center / contain\r\n      no-repeat;\r\n  }\r\n  &__timer-image {\r\n    width: 85px;\r\n    background: url('./../../../assets/svg/timeIcon.svg') center / contain\r\n      no-repeat;\r\n  }\r\n\r\n  &__timer-checkbox-container,\r\n  &__volume-checkbox-container {\r\n    position: relative;\r\n    .custom__checkbox {\r\n      position: absolute;\r\n      cursor: pointer;\r\n      top: 50%;\r\n      left: 50%;\r\n      transform: translate(-50%, -90%);\r\n      width: 40px;\r\n      height: 40px;\r\n      border: 1px solid $range-disabled;\r\n      background: $range;\r\n      border-radius: 5px;\r\n      &:before {\r\n        content: '';\r\n        position: absolute;\r\n        display: none;\r\n        left: 11px;\r\n        top: -2px;\r\n        width: 17px;\r\n        height: 30px;\r\n        border: solid $element-enabled;\r\n        border-width: 0px 5px 5px 0px;\r\n        transform: rotate(40deg);\r\n      }\r\n    }\r\n\r\n    input[type='checkbox'] {\r\n      width: 40px;\r\n      height: 40px;\r\n      margin: 20px 0;\r\n      opacity: 0;\r\n    }\r\n  }\r\n}\r\ninput[type='checkbox']:checked + .custom__checkbox::before {\r\n  display: block;\r\n}\r\n","* {\r\n\tpadding: 0;\r\n\tmargin: 0;\r\n\tborder: 0;\r\n}\r\n\r\n*,\r\n*:before,\r\n*:after {\r\n\t-webkit-box-sizing: border-box;\r\n\tbox-sizing: border-box;\r\n}\r\n\r\n:focus,\r\n:active {\r\n\toutline: none;\r\n}\r\n\r\na:focus,\r\na:active {\r\n\toutline: none;\r\n}\r\n\r\nnav,\r\nfooter,\r\nheader,\r\naside {\r\n\tdisplay: block;\r\n}\r\n\r\nhtml,\r\nbody {\r\n\theight: 100%;\r\n\twidth: 100%;\r\n\tfont-size: 100%;\r\n\tline-height: 1;\r\n\tfont-size: 14px;\r\n\t-ms-text-size-adjust: 100%;\r\n\t-moz-text-size-adjust: 100%;\r\n\t-webkit-text-size-adjust: 100%;\r\n}\r\n\r\ninput,\r\nbutton,\r\ntextarea {\r\n\tfont-family: inherit;\r\n}\r\n\r\ninput::-ms-clear {\r\n\tdisplay: none;\r\n}\r\n\r\nbutton {\r\n\tcursor: pointer;\r\n}\r\n\r\nbutton::-moz-focus-inner {\r\n\tpadding: 0;\r\n\tborder: 0;\r\n}\r\n\r\na,\r\na:visited {\r\n\ttext-decoration: none;\r\n\tcolor: inherit;\r\n}\r\na:hover {\r\n\ttext-decoration: none;\r\n\tcolor: white;\r\n}\r\n\r\nul li {\r\n\tlist-style: none;\r\n}\r\n\r\nimg {\r\n\tvertical-align: top;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n\tfont-size: inherit;\r\n\tfont-weight: inherit;\r\n}\r\n"],"sourceRoot":""}]);
